import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  ENERGY,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EXPORT_NAME
} from '@/utils/constantsFile';
import {
  EnergyGetMeterDropdown,
  GetEnergyConsumptionStatistics
} from '@/api/energymonitoring';
// import { normalizeComponent } from 'vue-runtime-helpers';
export default {
  name: 'echarts',
  data() {
    return {
      ENERGY: ENERGY,
      TITLE: TITLE,
      BTN_TEXT: BTN_TEXT,
      dateTime: '',
      options: [],
      startTime: '',
      endTime: '',
      selectValue: '',
      echartsHref: null,
      downloadName: null,
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      }
    };
  },
  created() {
    // 调用列表查询接口
    this.findDevice();
    this.$nextTick(function() {
      this.showEcharts();
    });
  },
  mounted() {},
  methods: {
    /**
     * @description:查询数据
     * @param {type}
     * @return
     * @author: CH
     */
    search() {
      if (this.dateTime !== '') {
        this.startTime = this.dateTime[0];
        this.endTime = this.dateTime[1];
      }
      this.showEcharts();
    },
    /**
     * @description:重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.dateTime = '';
      this.selectValue = '';
      this.startTime = '';
      this.endTime = '';
      this.showEcharts();
    },
    /**
     * @description:展示图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showEcharts() {
      try {
        const res = await GetEnergyConsumptionStatistics({
          type: 'LIQUID',
          deviceCode: this.selectValue,
          start: this.startTime,
          end: this.endTime
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows;
          // 基于准备好的dom，初始化echarts实例
          var myChart = this.$echarts.init(
            document.getElementById('showEcharts')
          );
          // 绘制图表
          myChart.setOption({
            title: {},
            tooltip: {
              trigger: 'axis'
            },
            grid: {
              left: '2%',
              right: '4%',
              bottom: '4%',
              top: '16%',
              containLabel: true
            },
            xAxis: {
              type: 'category',
              boundaryGap: true,
              data: data.dateTime,
              axisLabel: {
                show: true
              }
            },
            yAxis: {
              type: 'value',
              scale: true
            },
            series: [
              {
                name: ENERGY.DAY_ENERGY,
                type: 'line',
                stack: ENERGY.ALL,
                // symbol: 'none',
                symbol: 'circle',
                symbolSize: 0.01,
                label: {
                  show: true,
                  position: 'top',
                  color: 'auto'
                },
                itemStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                lineStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                data: data.dayConsumption
              }
            ]
          });
          // this.getEchartURL();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:查询设备
     * @param{row}
     * @return:{json}
     * @author: CH
     */
    async findDevice() {
      try {
        const res = await EnergyGetMeterDropdown(
          {
            type: 'LIQUID'
          },
          [
            {
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '2' // 参数值
            }
          ]
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          this.options = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:倒入下载路径
     * @param {type}
     * @return:
     * @author: CH
     */
    getEchartURL() {
      var myChart = this.$echarts.init(document.getElementById('showEcharts'));
      var i = myChart.getDataURL({
        type: 'png',
        backgroundColor: 'white'
        // 导出的图片分辨率比例，默认为 1。
      });
      this.echartsHref = i;
      this.downloadName = EXPORT_NAME.LIQUI_STATISTICS;
      setTimeout(() => {
        this.$refs.export.click();
      }, 200);
    },
    /**
     * @description:下载
     * @param {type}
     * @return:
     * @author: CH
     */
    exportEchart() {
      this.getEchartURL();
      // this.$refs.export.click();
    }
  },
  components: {}
};
