    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findDayList() {
      try {
        this.tableDay.tableLoading = true;
        const res = await GetCurrentLevelDayEnergyConsumption({
          endTime: this.dateTime[1] + ' 23:59:59',
          startTime: this.dateTime[0] + ' 00:00:00',
          deviceCode: this.rows.deviceCode,
          groupId: this.rows.groupId,
          groupType: this.groupType,
          size: this.tableDay.tablePage.pageSize,
          current: this.tableDay.tablePage.currentPage
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          this.tableDay.tableData = [];
          for (var i in res.rows.dateTime) {
            var json = {};
            if (res.rows.now !== undefined) {
              json = {
                dateTime: res.rows.dateTime[i],
                dayConsumption: res.rows.dayConsumption[i],
                now: res.rows.now[i]
              };
            } else {
              json = {
                dateTime: res.rows.dateTime[i],
                dayConsumption: res.rows.dayConsumption[i]
              };
            }

            this.tableDay.tableData.push(json);
          }
          // 将总条数写入table分页配置
          this.tableDay.tablePage.total = res.total;
          // 加载动画消失
          this.tableDay.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:展示车间7日奇书图形
     * @param {type}
     * @return:
     * @author: CH
     */
    async showDayEcharts() {
      try {
        const res = await GetCurrentLevelDayEnergyConsumption({
          endTime: this.dateTime[1] + ' 23:59:59',
          startTime: this.dateTime[0] + ' 00:00:00',
          deviceCode: this.rows.deviceCode,
          groupId: this.rows.groupId,
          groupType: this.groupType,
          size: 99999,
          current: 1
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入选项
          var data = res.rows;
          // 基于准备好的dom，初始化echarts实例
          var myChart = this.$echarts.init(
            document.getElementById('dayEcharts')
          );
          // 绘制图表
          myChart.setOption({
            // title: {
            //   text: ENERGY.SEVEN_DAYS_ECHARTS,
            //   x: 'center',
            //   y: 'top'
            // },
            tooltip: {
              trigger: 'axis'
            },
            grid: {
              left: '2%',
              right: '4%',
              bottom: '4%',
              top: '16%',
              containLabel: true
            },
            xAxis: {
              type: 'category',
              splitLine: { show: false },
              boundaryGap: true,
              data: data.dateTime,
              axisLabel: {
                show: true
              }
            },
            dataZoom: [
              {
                type: 'slider',
                show: true,
                xAxisIndex: [0],
                left: '9%',
                bottom: 10,
                start: 10,
                end: 90
              }
            ],
            yAxis: {
              type: 'value',
              scale: true,
              splitLine: { show: false }
            },
            series: [
              {
                name: ENERGY.LEVEL_DIFFERENCE_NOUNIT,
                type: 'line',
                stack: ENERGY.ALL,
                areaStyle: {},
                smooth: true,
                lineStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                itemStyle: {
                  normal: {
                    color: '#1b89f7'
                  }
                },
                data: data.dayConsumption
              },
              {
                name: ENERGY.CURRENT_LEVEL,
                type: 'line',
                stack: ENERGY.ALL,
                areaStyle: {},
                smooth: true,
                lineStyle: {
                  normal: {
                    color: 'red'
                  }
                },
                itemStyle: {
                  normal: {
                    color: 'red'
                  }
                },
                data: data.now
              }
            ]
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

