    /**
     * @description: 下载
     * @param {type}
     * @return:
     * @author: CH
     */
    async download() {
      try {
        const params = {
          endTime: this.dateTime[1] + ' 23:59:59',
          startTime: this.dateTime[0] + ' 00:00:00',
          deviceCode: this.rows.deviceCode,
          groupId: this.rows.groupId,
          groupType: this.groupType
        };
        const res = await exportEnergyMonitorHistory(params);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          var filename = '';
          filename = this.rows.name + EXPORT_NAME.DAILY_LEVEL_MONITORING;
          this.$options.filters.setExportFunc(res, filename);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    }
