// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  EXPORT_NAME,
  ENERGY,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  PAGE_TITLE
} from '@/utils/constantsFile';

// 导入所需接口
import {
  GetGroupConsumption,
  GetLiquidLevelGaugeConsumption
} from '@/api/energymonitoring';
import { exportEnergyMonitor } from '@/api/export';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    groupType: String,
    showDialog: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      tableLoading: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      tableData: [],
      // table表头配置
      tableColumns: [
        {
          field: 'name',
          title: PAGE_TITLE.WORKSHOP_NAME,
          width: '',
          align: 'center'
          //  // sortable: true
        },
        {
          field: 'dayEnergyConsumption',
          title: ENERGY.DAY_LEVEL_DIFFERENCE,
          width: '',
          align: 'center'
          //  // sortable: true
        },
        {
          field: 'weekConsumption',
          title: ENERGY.DAY_LEVEL_DIFFERENCE,
          width: '',
          align: 'center'
          //  // sortable: true
        },
        {
          field: 'monthConsumption',
          title: ENERGY.MMONTH_LEVEL_DIFFERENCE,
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: 'allConsumption',
          title: ENERGY.HISTORY_LEVEL_DIFFERENCE,
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '',
          align: 'center',
          type: 'html',
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button
                    type="text"
                    onClick={() => this.success(row, column, false)}
                  >
                    {BTN_TEXT.HISTORICAL_DATA}
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],
      tableColumn: [
        {
          field: 'deviceName',
          title: ENERGY.LIQUID_LEVEL_METER_NAME,
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: 'hourConsumption',
          title: ENERGY.CURRENT_LEVEL,
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: 'time',
          title: ENERGY.UPDATE_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '',
          align: 'center'
          // sortable: true
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '',
          align: 'center',
          type: 'html',
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button
                    type="text"
                    onClick={() => this.success(row, column, false)}
                  >
                    {BTN_TEXT.HISTORICAL_DATA}
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  watch: {
    // 发现type发生变化触发事件
    groupType(val) {
      if (val !== '4') {
        this.showTable = true;
        if (val !== '') {
          // this.tableColumns[4].title = ENERGY.ALLY_WATER_CONSUMPTION;
          if (val === '2') {
            this.tableColumns[0].title = PAGE_TITLE.WORKSHOP_NAME;
          } else {
            this.tableColumns[0].title = PAGE_TITLE.PRODUCTION_LINE_NAME;
          }
          this.onTablePageChange(1, 10);
        } else {
          this.groupType = '';
          this.tableColumns[0].title = ENERGY.DEVICENAME;
          // this.tableColumns[4].title = ENERGY.GAS_METER_RECORD;
          this.onTablePageChange(1, 10);
        }
      } else {
        this.showTable = false;
        this.findLevelList();
      }

      // this.restaurants = [];
      // this.deviceName = '';
      // this.findList();
    }
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoadings = true;
        const res = await GetGroupConsumption({
          size: this.tablePages.pageSize,
          current: this.tablePages.currentPage,
          type: 'LIQUID',
          groupType: this.groupType
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          for (var i in res.rows) {
            if (this.groupType !== '') {
              this.tableDatas[i].name = this.tableDatas[i].groupName;
            } else {
              this.tableDatas[i].name = this.tableDatas[i].deviceName;
            }
          }
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:获取液位计表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findLevelList() {
      try {
        this.tableLoading = true;
        const res = await GetLiquidLevelGaugeConsumption({
          size: this.tablePage.pageSize,
          current: this.tablePage.currentPage
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableData = res.rows;
          for (var i in res.rows) {
            this.tableData[i].name = this.tableData[i].deviceName;
          }
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findLevelList();
    },
    /**
     * @description: 点击确认修改
     * @param row 当前行数据
     * @param column 表头内容
     * @return: 无
     * @author: CH
     */
    success(row, column, showFlag, Level) {
      if (this.showDialog) {
        this.showDialog(row, showFlag, Level);
      }
    },
    /**
     * @description: 获取表格数据
     * @param 无
     * @return: 无
     * @author: ch
     */
    async exportDatas() {
      try {
        const params = {
          type: 'LIQUID',
          groupType: this.groupType
        };
        const res = await exportEnergyMonitor(params);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          var filename = '';
          if (this.groupType === '2') {
            filename = ENERGY.WORKSHOP + EXPORT_NAME.LIQUI_MONITORING;
          } else if (this.groupType === '3') {
            filename = ENERGY.PRODUCTION_LINE + EXPORT_NAME.LIQUI_MONITORING;
          } else {
            filename = ENERGY.LIQUID_LEVELMETER + EXPORT_NAME.LIQUI_MONITORING;
          }
          this.$options.filters.setExportFunc(res, filename);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取表格数据
     * @param 无
     * @return: 无
     * @author: ch
     */
    async exportData() {
      try {
        const params = {};
        const res = await exportEnergyMonitor(params);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(
            res,
            ENERGY.LIQUID_LEVELMETER + EXPORT_NAME.LIQUI_MONITORING
          );
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
