// 引入未审核组件

import meterTable from '@/views/energy/LiquidLevelMonitoring/meterTable.vue';
import echarts from '@/views/energy/LiquidLevelMonitoring/echarts.vue';
import showInfo from '@/views/energy/LiquidLevelMonitoring/showInfo.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { ENERGY, STATUS_CODE, TITLE, BTN_TEXT } from '@/utils/constantsFile';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { GetEnergyConsumption } from '@/api/energymonitoring';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// // 引入审核通过组件
// import waterMeter from '@/views/main/EnergyAllocation/waterMeter.vue';
// // 引入审核未通过组件
// import gasMeter from '@/views/main/EnergyAllocation/gasMeter.vue';
export default {
  name: 'LiquidLevelMonitoring',
  data() {
    return {
      ENERGY: ENERGY,
      BTN_TEXT: BTN_TEXT,
      title: '',
      groupType: '2',
      levelFlag: false,
      hackReset: false,
      dayEnergyConsumption: '0',
      yesterdayEnergyConsumption: '0',
      // tab默认激活下标
      active: 0,
      rows: {},
      showFlag: true,
      // 默认引入的组件名称
      currentView: 'workshop',
      dialogVisible: false,
      tabs: [
        {
          type: 'workshop',
          name: ENERGY.WORKSHOP,
          value: '2'
        },
        {
          type: 'productionLine',
          name: ENERGY.PRODUCTION_LINE,
          value: '3'
        },
        {
          type: 'electricityMeter',
          name: '液位计',
          value: '4'
        }
      ]
    };
  },
  created() {
    // 调用列表查询接口
    this.getEnergyConsumption();
  },
  methods: {
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.groupType = v;
    },
    /**
     * @description:获取今日和昨日能耗
     * @param {type}
     * @return:
     * @author: CH
     */
    async getEnergyConsumption() {
      try {
        // this.tableLoading = true;
        const res = await GetEnergyConsumption({
          type: 'LIQUID'
        });
        if (res.code) {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 调用列表查询接口
            this.dayEnergyConsumption = res.rows.dayEnergyConsumption;
            this.yesterdayEnergyConsumption =
              res.rows.yesterdayEnergyConsumption;
            // 提示异常信息
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    Refresh() {
      if (this.groupType !== '4') {
        this.$refs.meterTable.onTablePageChange(1, 10);
      } else {
        this.$refs.meterTable.onTablePageChanges(1, 10);
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    download() {
      if (this.groupType !== '4') {
        this.$refs.meterTable.exportDatas();
      } else {
        this.$refs.meterTable.exportData();
      }
    },
    /**
     * @description:打开弹窗
     * @param {type}
     * @return:
     * @author: CH
     */
    showDialog(row, showFlag) {
      this.dialogVisible = true;
      this.rows = row;
      this.showFlag = showFlag;
      this.levelFlag = false;
      if (this.groupType === '4') {
        this.levelFlag = true;
      }
      this.$nextTick(() => {
        this.hackReset = true; // 重建组件
      });
      if (showFlag) {
        if (this.groupType !== '') {
          if (this.groupType !== '4') {
            this.title = row.groupName + ENERGY.IS_INFO;
          } else {
            this.title = row.deviceName + ENERGY.IS_INFO;
          }
        } else {
          this.title = row.deviceName + ENERGY.IS_INFO;
        }
      } else {
        if (this.groupType !== '') {
          if (this.groupType !== '4') {
            this.title = row.groupName + ENERGY.IS_HISTORY;
          } else {
            this.title = row.deviceName + ENERGY.IS_HISTORY;
          }
        } else {
          this.title = row.deviceName + ENERGY.IS_HISTORY;
        }
      }
    },
    handleClose() {
      this.dialogVisible = false;
      this.hackReset = false;
    },
    /**
     * @description:返回主页面
     * @param {type}
     * @return:
     * @author: CH
     */
    back() {
      this.showFlag = true;
    }
  },
  components: {
    // 加载未审核组件
    meterTable,
    echarts,
    HeaderTitle,
    showInfo,
    HeadOperationItem
  }
};
